import Vue from 'vue'
import VueI18n from 'vue-i18n'

Vue.use(VueI18n)

const messages = {
  ru: {
    message: {
      hello: 'hello world'
    }
  },
  kz: {
    message: {
      hello: 'こんにちは、世界'
    }
  }
}

const i18n = new VueI18n({
  locale: 'ru', 
  fallbackLocale: 'ru',
  messages
})

export default i18n


